export default {
	data() {
		return {
			indexTab:1,
			isRotate:false,
			param: {
				account: 'admin',
				password: '123456',
				mobile: '17080057443',
				captcha: '1111',
			},
		}
	},
	methods: {
		loginSuccess(){
			uni.reLaunch({
				url:'../app/index'
			})
		},
		Toggle(index){
			console.log('test')
			this.indexTab=index;
		},
		// 登录
		login() {
			// 判断是否加载中，避免重复点击请求
			if (this.isRotate) {
				this.$refs['myMessage'].warn('Please Wait Patiently')
			} else if (this.param.account == '' || this.param.password.length < 6) {
				this.$refs['myMessage'].warn('Account or Password Error')
			} else {
				this.isRotate = true;
				setTimeout(() => {
					this.$api.login(this.param, res => {
						if (res.code) {
							this.$common.saveUserInfo(res.data);
							this.$refs['myMessage'].success(res.msg);
							this.loginSuccess();
						} else {
							this.$refs['myMessage'].err(res.msg);
						}
					}, () => {
						this.isRotate = false;
					});
				}, 1000);
			}
		},
	},
	onLoad() {
		console.log('onLoad')
	}
}
